import exifr from "exifr";

(() => {
  const overlay = document.querySelector('.image-overlay');
  const img = overlay ? overlay.querySelector('.image-overlay-img') : null;
  const exifEl = overlay ? overlay.querySelector('.image-overlay-exif') : null;
  const backdrop = overlay ? overlay.querySelector('.image-overlay-backdrop') : null;
  const thumbs = Array.from(document.querySelectorAll('.gallery-thumb'));
  if (!overlay || !img || !exifEl || thumbs.length === 0) return;

  let token = 0;

  const formatExposure = (exposure) => {
    if (!exposure) return null;
    if (exposure >= 1) return `${exposure}s`;
    const denom = Math.round(1 / exposure);
    return `1/${denom}`;
  };

  const formatExif = (data) => {
    if (!data) return null;
    const parts = [];
    const model = [data.Make, data.Model].filter(Boolean).join(' ').trim();
    if (model) parts.push(model);
    const lens = [data.LensMake, data.LensModel].filter(Boolean).join(' ').trim()
      || (data.LensModel || data.Lens);
    if (lens) parts.push(lens);
    if (data.FocalLength) parts.push(`${Math.round(data.FocalLength)}mm`);
    if (data.FNumber) parts.push(`f/${Number(data.FNumber).toFixed(1)}`);
    const exposure = formatExposure(data.ExposureTime);
    if (exposure) parts.push(exposure);
    if (data.ISO) parts.push(`ISO ${data.ISO}`);
    return parts.length ? parts.join('  ') : null;
  };

  const setExifText = (text) => {
    exifEl.textContent = text || 'No EXIF info';
  };

  const open = async (src, title) => {
    img.src = src;
    img.alt = title || '';
    overlay.classList.add('open');
    overlay.setAttribute('aria-hidden', 'false');
    document.body.classList.add('no-scroll');

    const current = ++token;
    setExifText('Loading EXIF...');

    try {
      const response = await fetch(src, { mode: 'cors' });
      if (!response.ok) throw new Error('fetch failed');
      const blob = await response.blob();
      const data = await exifr.parse(blob);
      if (current !== token) return;
      const formatted = formatExif(data);
      setExifText(formatted || 'No EXIF info');
    } catch (err) {
      if (current !== token) return;
      setExifText('No EXIF info');
    }
  };

  const close = () => {
    overlay.classList.remove('open');
    overlay.setAttribute('aria-hidden', 'true');
    img.src = '';
    exifEl.textContent = '';
    document.body.classList.remove('no-scroll');
  };

  thumbs.forEach((btn) => {
    btn.addEventListener('click', () => {
      const src = btn.getAttribute('data-full');
      if (!src) return;
      open(src, btn.getAttribute('data-title'));
    });
    btn.addEventListener('contextmenu', (event) => {
      event.preventDefault();
    });
  });

  overlay.addEventListener('click', (e) => {
    if (e.target === img || e.target === backdrop || e.target === overlay) close();
  });

  overlay.addEventListener('contextmenu', (event) => {
    event.preventDefault();
  });

  img.addEventListener('contextmenu', (event) => {
    event.preventDefault();
  });

  document.addEventListener('keydown', (e) => {
    if (e.key === 'Escape') close();
  });
})();
